.rec {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 30px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: rgb(223, 160, 160);
    align-items: center;
    width: 500px;
    color: rgb(10, 7, 7);
    padding: 2rem;
    text-align: left;
}

.rec img {
    border-radius: 50%;
    margin: 10px;
    border: 2px solid black;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
}